/* DELCARE VARIABLES */
/* ================================================= */
$white: #ffffff;
$black: #1c1c1c;
$grey: rgba(#e1e1e1, .5);
$grey--light: rgba(#fbfbfc, .1);
$grey: #e1e1e1;
$grey--light: #fbfbfc;
$grey--dark: #212121;

$font-md: 1em;
$font-lg: 2em;

$sans: "Apercu Regular", Calibri, sans-serif;
/* ================================================= */

html, body {
  padding: 0;
  margin: 0;
  font-style: $sans;
  font-size: $font-md;
  background: $black;
}

#h-container {
  padding: 20px;
  margin: 20px;

}
